.quiz-title {
    position: fixed;
    top: 0px;
    left: 46%;
    transform: translateX(-50%);
    font-size: 1.8rem; /* –£–º–µ–Ω—å—à–µ–Ω–Ω—ã–π —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ */
    color: #00d1ff;
    text-shadow: 0px 0px 10px #00d1ff, 0px 0px 20px #0077ff, 0px 0px 30px #00d1ff;
    font-weight: bold;
    pointer-events: none; /* –ß—Ç–æ–±—ã –Ω–µ –º–µ—à–∞–ª–æ –∫–ª–∏–∫–∞–º */
    white-space: nowrap; /* –ó–∞–ø—Ä–µ—â–∞–µ—Ç –ø–µ—Ä–µ–Ω–æ—Å —Å—Ç—Ä–æ–∫–∏ */
    z-index: 2000 !important;
    max-width: 90vw; /* –û–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ —à–∏—Ä–∏–Ω—ã */
}

  
.instruction-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    background-size: cover;
    background-position: center;
    background-image: url("../assets/png/index-screen4.png"); /* –§–æ–Ω —Å—Ç–∞—Ç–∏—á–Ω—ã–π */
    
}

.background-image{
    filter: blur(20px);
}
.dark-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(4px);
}

.highlight-box {
    position: absolute;
    border: 3px solid #ffcc00;
    border-radius: 10px;
    box-shadow: 0 0 20px #ffcc00;
    transition: all 0.3s ease-in-out;
    background: transparent;
    z-index: 1002;
}

.instruction-box {
    position: absolute; /* ‚úÖ –î–æ–±–∞–≤–∏–ª, —á—Ç–æ–±—ã –º–µ–Ω—è–ª–∞—Å—å –ø–æ–∑–∏—Ü–∏—è */
    background: rgba(255, 0, 0, 0.286);
    padding: 15px;
    border-radius: 10px;
    text-align: center;
    font-size: 18px;
    font-weight: 900; /* –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è –∂–∏—Ä–Ω–æ—Å—Ç—å */
}

.next-button {
    background: #61ed25;
    color: black;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
    border-radius: 5px;
    margin-top: 10px;
    font-weight: bold;
}

.next-button:hover {
    background: #40b715 !important; /* –ù–æ–≤—ã–π —Ü–≤–µ—Ç –ø—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
    color: white; /* –ú–µ–Ω—è–µ—Ç —Ü–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ */
}

