body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f0f0;
}

.game-container {
  text-align: center;
  position: relative; /* –î–ª—è –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –±–ª—é—Ä–∞ */
}

.background-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0); /* –ü—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω */
  z-index: -1; /* –ü–æ–¥ –≤—Å–µ–º–∏ —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏ */
  transition: filter 0.5s ease;
}

.background-overlay.blurred {
  filter: blur(5px); /* –ë–ª—é—Ä —Ñ–æ–Ω–∞ */
}

.game-container h1 {
  position: relative;
  top: -40px;
  margin: 0;
  font-size: 26px;
  background: rgba(51, 66, 161, 0.684); /* –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω */
  padding: 5px 15px; /* –ö–æ–º–ø–∞–∫—Ç–Ω—ã–µ –æ—Ç—Å—Ç—É–ø—ã */
  border-radius: 18px; /* –ó–∞–∫—Ä—É–≥–ª–µ–Ω–Ω—ã–µ –∫—Ä–∞—è */
  display: inline-block; /* –ß—Ç–æ–±—ã –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –ø–æ–¥—Å—Ç—Ä–æ–∏–ª—Å—è –ø–æ–¥ —Ä–∞–∑–º–µ—Ä —Ç–µ–∫—Å—Ç–∞ */
}



.matryoshkas {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-top: 20px;

  z-index: 10; /* –ß—Ç–æ–±—ã –º–∞—Ç—Ä–µ—à–∫–∏ –±—ã–ª–∏ –Ω–∞–¥ –±–ª—é—Ä–æ–º */
}

.matryoshka {
  position: relative;
  cursor: pointer;
  width: 100px;
  height: 200px;
  top: -30px; /* –ü–æ–¥–Ω–∏–º–∞–µ–º –º–∞—Ç—Ä–µ—à–∫–∏ */
  filter: drop-shadow(0 4px 6px rgba(0, 0, 0, 0.2)); /* –¢–µ–Ω—å –ø–æ–¥ –º–∞—Ç—Ä–µ—à–∫–æ–π */
  z-index: 10; /* –ß—Ç–æ–±—ã –º–∞—Ç—Ä–µ—à–∫–∏ –±—ã–ª–∏ –Ω–∞–¥ –±–ª—é—Ä–æ–º */
}

.matryoshka-body img,
.matryoshka-top img {
  width: 125%;
  height: auto;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}


.matryoshka-top {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10; /* –ß—Ç–æ–±—ã –≤–µ—Ä—Ö–Ω—è—è —á–∞—Å—Ç—å –º–∞—Ç—Ä–µ—à–∫–∏ –±—ã–ª–∞ –Ω–∞–¥ –±–ª—é—Ä–æ–º */
}



.promo-ticket-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5); /* –ü–æ–ª—É–ø—Ä–æ–∑—Ä–∞—á–Ω—ã–π —Ñ–æ–Ω */
  z-index: 20; /* –ö–∞—Ä—Ç–æ—á–∫–∞ –ø—Ä–æ–º–æ–∫–æ–¥–∞ –ø–æ–≤–µ—Ä—Ö –≤—Å–µ–≥–æ */
}
.promo-ticket-container {
  background: linear-gradient(135deg, #ab0e0e, #cfd44e);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
  text-align: center;
  color: white;
  transform: scale(1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.promo-ticket-container:hover {
  transform: scale(1.02);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.3);
}

.promo-ticket h3 {
  font-size: 24px;
  margin-bottom: 10px;
}

.promo-ticket p {
  font-size: 18px;
  margin: 5px 0;
}

.promo-code-text {
  font-size: 22px;
  font-weight: bold;
  margin: 15px 0;
}

.copy-button {
  background-color: rgb(20, 174, 225);
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.1s ease;
}

.copy-button:hover {
  background-color: rgb(0, 140, 210);
}

.copy-button:active {
  transform: scale(0.95);
}

.notification {
  position: fixed;
  top: -30%;
  left: 10%;
  transform: translate(-50%, -50%);
  background: rgba(20, 174, 225, 0.512);
  color: #ffffff93;
  font-size: 16px;
  padding: 14px 24px;
  border-radius: 12px;
  box-shadow: 0 4px 15px rgba(255, 255, 255, 0.29);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  max-width: 80%;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: 2px solid #ffffff7e;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  z-index: 20;
}


/* –£–±–∏—Ä–∞–µ–º –æ–±–≤–æ–¥–∫—É –ø—Ä–∏ —Ñ–æ–∫—É—Å–µ –∏ –∞–∫—Ç–∏–≤–Ω–æ–º —Å–æ—Å—Ç–æ—è–Ω–∏–∏ */
button:focus, button:active,
.matryoshka:focus, .matryoshka:active,
.matryoshka-top img:focus, .matryoshka-top img:active,
.matryoshka-body img:focus, .matryoshka-body img:active {
  outline: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); /* –£–±–∏—Ä–∞–µ–º –≤—ã–¥–µ–ª–µ–Ω–∏–µ –ø—Ä–∏ —Ç–∞–ø–µ –Ω–∞ iOS */
}

/* –£–±–∏—Ä–∞–µ–º –æ–±–≤–æ–¥–∫—É –¥–ª—è –≤—Å–µ—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤, –µ—Å–ª–∏ –Ω—É–∂–Ω–æ */
*:focus {
  outline: none;
}