
/* –ö–Ω–æ–ø–∫–∞ "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å" - –±–∞–∑–æ–≤—ã–µ —Å—Ç–∏–ª–∏ */
.restart-button-container {
    position: absolute ;
    left: 50% ;
    transform: translateX(-50%) ;
    width: 100% ;
    display: flex ;
    justify-content: center;
    z-index: 20;
}

/* –ö–Ω–æ–ø–∫–∞ "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å" –ø—Ä–∏ –≤—ã–∏–≥—Ä—ã—à–µ (–æ–ø—É—â–µ–Ω–∞ –Ω–∞ 20px –≤–Ω–∏–∑) */
.restart-button-container.restart-bottom {
    bottom: -2px ;
}

/* –ö–Ω–æ–ø–∫–∞ "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å" –ø—Ä–∏ –ø—Ä–æ–∏–≥—Ä—ã—à–µ (–ø–æ–¥–Ω—è—Ç–∞ –Ω–∞ 40px) */
.restart-button-container.restart-center {
    bottom: 150px;
}

/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ "–í—ã –ø—Ä–æ–∏–≥—Ä–∞–ª–∏" (–æ–ø—É—â–µ–Ω –Ω–∞ 50px) */
.result-text.lose {
    margin-top: 100px;
}


